import { useState } from "react";

const App = () => {
  const [todos, setTodos] = useState([]);

  return (
    <>
      <h1>Todo App</h1>
      <input type="text" name="todo" />
      <button>Add</button>
      <ul>
        {todos.map(todo => {
          return <li>todo</li>;
        })}
      </ul>
    </>
  );
};

export default App;
